sans-serif; */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  min-height: 100vh;
  background: linear-gradient(
    -90deg,
    #0b142d,
    #21163f,
    #531c86,
    #631c81,
    #902bbb
  );
  background-size: 400% 400%;
  -webkit-animation: gradient 9s ease infinite;
          animation: gradient 9s ease infinite;
  transition: 1s ease;
}

@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

.midnight {
  position: absolute;
  width: 100%;
  height: 20px;
  top: 15%;
  margin-top: -10px;
  text-align: center;
  font-family: "Varela Round", sans-serif;
  color: white;
  font-weight: 100;
  font-size: 4em;
}

.choose {
  position: absolute;
  width: 100%;
  height: 20px;
  top: 25%;
  margin-top: -10px;
  text-align: center;
  font-family: "M PLUS Rounded 1c", sans-serif;
  color: #ffa65e;
  font-weight: 800;
  font-size: 1.5em;
}

.gif-1,
.gif-2,
.gif-3 {
  margin: 0 5em;

  height: 15em;
  width: auto;
  border-radius: 30px;
}

.gifs {
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-top: 13em;
}

.gifs p {
  color: #ffa65e;
  text-align: center;
  font-family: "M PLUS Rounded 1c", sans-serif;
  font-weight: 800;
  font-size: 1.3em;
  text-decoration: none;
}

#buttons {
  display: flex;
  justify-content: space-around;
  width: 15em;
  margin-right: 50%;
  margin-left: 50%;
  transform: translate(-50%);
  font-family: "Muli", sans-serif;
  margin-top: 2em;
}

#buttons button {
  height: 3em;
  width: 6em;
  background-color: #21163f;
  color: white;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  transition: 0.2s;
}

#buttons button:hover {
  background-color: #ffffff;

  color: #0b142d;
}

.All-1 {
    position: absolute;
    width: 15em;
    height: 35em;
    margin-top: -35em;
    right: 3em;
    text-align: center;
    padding: 2em 0em;
    background-color: rgb(76, 20, 141);
    border-radius: 25px;

}

h1 a {
    color: white;
    cursor: pointer;

}

.mini-gif-1,
.mini-gif-2,
.mini-gif-3 {
    margin: 0 1em;

    height: 5em;
    width: auto;
    border-radius: 15px;
    cursor: pointer;

}


.anchors {
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: white;
    padding: 2em 0em;
    cursor: default;
}
